
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.octicon {
  display: inline-block;
  fill: currentColor;
}
.octicon > path {
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
}
.octicon-flip-horizontal > path {
  -webkit-transform: scale(-1, 1);
          transform: scale(-1, 1);
}
.octicon-flip-vertical > path {
  -webkit-transform: scale(1, -1);
          transform: scale(1, -1);
}
.octicon-spin > path {
  -webkit-animation: octicon-spin 1s 0s infinite linear;
          animation: octicon-spin 1s 0s infinite linear;
}
.octicon-inverse {
  color: #fff;
}
@-webkit-keyframes octicon-spin {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes octicon-spin {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
